<template>
    <div class='fazhan'>
        <div v-html="about.content"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            about:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.apis.get(_this.apis.about,{params:{lang:_this.apis.lang,id:4}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.about = res.data.data
            }
        })
    },
    watch:{
    },
    methods:{
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.fazhan{
    width: 1200px;
    min-height: 600px;
    margin: 0 auto;
    margin-bottom: 100px;
}
</style>


